.App {
  text-align: center;
}

html {
  scroll-behavior: smooth;
  overflow-x:hidden;
}

/*--------------------------------------------------------------------------------------------------------------------------*/
/*-----------------------------------------Header---------------------------------------------------------------------------*/
/*--------------------------------------------------------------------------------------------------------------------------*/


/*holds navbar, matches background for large screens*/
.navbar-container {
  background-color:transparent !important;
  width:100%;
  height:100px;
  z-index:5;
  position:fixed;
  top:0px;
}

#white-nav {
  background-color:transparent !important;
}

.collapse ul {
  background-color:white;
}

nav {
  position:fixed !important;
  top:0px;
  width:100%;
  z-index:5;
}

.navbar-brand img{
  height:24px;
  margin-top:-6.5px;
}

.navbar-brand a:hover {
  color:lightblue !important;
}

.book-now {
  padding-right:10px;
  background-color:transparent;
  color:white;
}

.nav-item {
  padding-left:10px;
  padding-right:10px;
  font-size:14px;
  font-weight:500;
  letter-spacing:1px;
}

.nav-item a {
  color: #ff6700 !important;
}

ul {
  padding:10px;
}

li a {
  color:#ff6700 !important;
  line-height:30px;
}

a:link {
  text-decoration: none !important;
}

li a:hover {
  color:lightblue !important;
}

.dropdown .nav-link {
  padding:0px;
  padding-left:20px;
}

.no-padding-nav {
  padding:0px !important;
  padding-left:20px !important;
}

.padded-nav {
  padding-left:20px;
}

.navbar-nav {
  text-align:right;
}

.navbar-light .navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(255,255,255, 0.8)' stroke-width='3' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E") !important;
}

.navbar-toggler {
  border-color: transparent !important;
}


@media only screen and (min-width: 900px) {
  nav {
    width:940px;
    left:50%;
    margin-left:-470px;
  }
}

/*--------------------------------------------------------------------------------------------------------------------------*/
/*-----------------------------------------Home-----------------------------------------------------------------------------*/
/*--------------------------------------------------------------------------------------------------------------------------*/

.main {
  height:100vh;
  overflow:hidden; 
  width:100%;
  background-position: center !important;
  background-repeat:no-repeat;
  background-size:cover;
  background-attachment:fixed;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index:-5;
}

.main-img-1 {  
  background-image: url(./home-back.jpg);
}

.main-img-2 {  
  background-image: url(./home-back2.jpg);
}

.main-img-3 {  
  background-image: url(./home-back3.jpg);
}

.main-img-4 {  
  background-image: url(./home-back4.jpg);
}


.main-slider .slick-list {
  height:100vh;
}

.main-slider .slick-dots {
  bottom: 50px !important;
  z-index:5 !important;
  background-color: transparent !important; 
}

.main-slider .slick-dots li button::before {
  font-size:10px !important;
}

.main-slider .slick-dots li button:focus {
  opacity: .1 ;
}

.main-slider .slick-dots li.slick-active button {
  opacity: 1 !important;
}

.main-slider .slick-dots li button:hover, .main-slider .slick-dots li.slick-active button:hover {
  opacity: 1 !important;
}

.main-slider .slick-arrow {
  display: none !important;
}

@media only screen and (max-width: 700px) {
  .main {
    background-attachment: scroll;
  }
}


/*-----------------------------------------your-stay------------------------------------------------------------------------*/
/*--------------------------------------------------------------------------------------------------------------------------*/

.your-stay {
  height:50vh;
  background-color:#8039c7;
  color:white;
  width:100%;
}

.your-stay-container {  
  max-width:100% !important;
}

.your-stay h2 {
  font-size:36px;
  margin:0;
}

.your-stay p {
  font-size:16px;
  text-align:center;
}

.your-stay .slick-arrow {
  display: none !important;
}

.your-stay-description {  
  display: flex;
  align-items: center;
  height: 50vh;
  text-align:center;
}

.your-stay-container {  
  width:90%;
  margin-left:auto;
  margin-right:auto;
}

.your-stay-carousel {
  margin-top:12.5vh !important;
}

.your-stay-img {
  height:50vh;
  overflow:hidden;
  background-position: center !important;
  background-repeat:no-repeat;
  background-size:cover;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index:1;
}

.your-stay-img-1 {  
  background-image: url(./home-back.jpg);
}

.your-stay-img-2 {  
  background-image: url(./home-back2.jpg);
}

.your-stay-img-3 {  
  background-image: url(./home-back3.jpg);
}

.your-stay-img-4 {  
  background-image: url(./home-back4.jpg);
}

@media only screen and (max-width: 767px) {
  .your-stay {
    height:70vh;
  }

  .your-stay-description {  
    display: flex;
    align-items: center;
    height: 25vh;
  }

  .your-stay-carousel {
    margin-top:5vh !important;
  }
}

/*-----------------------------------------happenings-----------------------------------------------------------------------*/
/*--------------------------------------------------------------------------------------------------------------------------*/

.happenings-description {
  width:100vw;
  text-align:center;
  margin-left:auto;
  margin-right:auto;
}

.happenings-description p {
  max-width:500px;
  margin-left:auto;
  margin-right:auto;
}

.grid {
  max-width:1000px;
  width:80%;
  margin-left:auto;
  margin-right:auto;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-template-rows: repeat(auto-fill, minmax(250px, 1fr));
  grid-auto-flow: dense;
}

.wide {
    grid-column: span 2;
}

.high {
    grid-row: span 2;
    min-height:500px;
}

.background-img {
  overflow:hidden;
  background-position: center !important;
  background-repeat:no-repeat;
  background-size:cover;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index:1;
}

.grid-2 {
  background-color:#febf3e;
}

.grid-3 {
  background-color:#8038c8;
}

.grid-6 {
  background-color:#47e1d9;
}

.grid-8 {
  background-color: #f97c44;
}

.top-left {
  background-image:url(./eating.jpg);  
}

.grid-1 {
  background-image:url(./happy-people.jpg);
}

.grid-4 {
  background-image:url(./concert.jpg);
}

.grid-5 {
  background-image:url(./pool.jpg);
}

.grid-7 {
  background-image:url(./coffee.jpg);
}

.space {
  width:100%;
  min-height:250px;
  min-width:250px;
  color:white;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align:center;
}


